import React from 'react';
import { RouteComponentProps } from '@reach/router';
import {
  Flex,
  Heading,
  FormLabel,
  FormErrorMessage,
  Avatar,
  PseudoBox,
  Button,
  useDisclosure,
  Box,
} from '@chakra-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { UserSelector } from '../../shared/store/user/user.selector';
import { setProfile } from '../../shared/store/user/user.slice';
import { Formik } from 'formik';
import { FormControl } from '@chakra-ui/core';
import { Input } from '../../shared/components/Input';
import { UserProfileApi } from '@selfplat/api-model/';
import { FormikErrors, FormikHelpers } from 'formik/dist/types';
import { UserService } from '../../shared/store/user/user.service';
import { ImageCropModal } from './ImageCropModal/ImageCropModal';

export interface ProfilePageProps extends RouteComponentProps {}

export const ProfilePage = (props: ProfilePageProps) => {
  const dispatch = useDispatch();
  const profile = useSelector(UserSelector.profile);
  const {
    isOpen: isModalOpen,
    onOpen: onOpenModal,
    onClose: onCloseModal,
  } = useDisclosure();

  const validateForm = (
    values: UserProfileApi,
  ): FormikErrors<UserProfileApi> => {
    const errors: FormikErrors<UserProfileApi> = {};
    const { firstName, lastName } = values;

    if (!firstName) {
      errors.firstName = 'First name is required';
    }

    if (!lastName) {
      errors.lastName = 'Last name is required';
    }

    return errors;
  };

  const submitForm = async (
    values: UserProfileApi,
    actions: FormikHelpers<UserProfileApi>,
  ) => {
    UserService.updateProfile({
      firstName: values.firstName,
      lastName: values.lastName,
    }).subscribe(updatedProfile => {
      dispatch(setProfile(updatedProfile));
      actions.setSubmitting(false);
    });
  };

  const onSaveProfilePicture = (imageBlob: string) => {
    if (!imageBlob) {
      return;
    }

    const fileName = `${profile.fullName}-avatar`;
    const imageFile = new File([imageBlob], fileName);
    const formData = new FormData();
    formData.append('image', imageFile);
    UserService.updateProfilePicture(formData).subscribe(updatedProfile => {
      dispatch(setProfile(updatedProfile));
    });
  };

  return (
    <Flex direction="column">
      <Heading as="h1" fontWeight="normal" letterSpacing={0.5} size="md" mb={5}>
        My Profile
      </Heading>

      <Flex direction="column">
        <Flex direction="column" p={16} bg="white" shadow="sm">
          <PseudoBox
            alignSelf="center"
            cursor="pointer"
            mb={10}
            p="2"
            borderRadius="50%"
            transition="all 1s"
            onClick={onOpenModal}
            _hover={{ bg: 'primary-50' }}
          >
            <Avatar
              width={56}
              height={56}
              name={profile.fullName}
              src={profile.pictureUrl}
            />
          </PseudoBox>

          <Box alignSelf="center" width="lg">
            <Formik
              initialValues={profile}
              onSubmit={submitForm}
              validate={validateForm}
            >
              {({
                handleSubmit,
                errors,
                isSubmitting,
                handleChange,
                handleBlur,
                values,
              }) => (
                <form onSubmit={handleSubmit}>
                  <FormControl isInvalid={Boolean(errors.firstName)}>
                    <FormLabel htmlFor="firstName">First name</FormLabel>
                    <Input
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.firstName}
                      id="firstName"
                    />
                    <FormErrorMessage>{errors.firstName}</FormErrorMessage>
                  </FormControl>
                  <FormControl mt={10} isInvalid={Boolean(errors.lastName)}>
                    <FormLabel htmlFor="lastName">Last name</FormLabel>
                    <Input
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.lastName}
                      id="lastName"
                    />
                    <FormErrorMessage>{errors.lastName}</FormErrorMessage>
                  </FormControl>

                  <FormControl mt={10} isDisabled>
                    <FormLabel htmlFor="email">Email</FormLabel>
                    <Input
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.email}
                      id="email"
                    />
                    <FormErrorMessage>{errors.email}</FormErrorMessage>
                  </FormControl>

                  <Flex mt={10} justifyContent="center">
                    <Button
                      alignSelf="flex-start"
                      type="submit"
                      fontSize="sm"
                      color="primary-500"
                      variant="outline"
                      isLoading={isSubmitting}
                    >
                      Save
                    </Button>
                  </Flex>
                </form>
              )}
            </Formik>
          </Box>
        </Flex>
      </Flex>

      <ImageCropModal
        isOpen={isModalOpen}
        onClose={onCloseModal}
        pictureUrl={profile.pictureUrl}
        onSaveImage={onSaveProfilePicture}
      />
    </Flex>
  );
};
