import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { CreateSelfDto, DreamApi, ProgressGoalApi, ResultGoalApi, SelfApi } from '@selfplat/api-model';
import {
  CreateResultGoalPayload,
  UpdateDreamTitlePayload,
  UpdateResultGoalTitlePayload,
  CreateProgressGoalPayload,
  UpdateProgressGoalTitlePayload,
  SetProgressGoalTitlePayload,
} from './self.type';

export interface SelfState {
  self: SelfApi;
  dream: DreamApi;
  resultGoal: ResultGoalApi;
  progressGoals: ProgressGoalApi[];
}

const initialState: SelfState = {
  self: null,
  dream: null,
  resultGoal: null,
  progressGoals: [],
};

const selfSlice = createSlice({
  name: 'self',
  initialState,
  reducers: {
    // Self
    fetchSelfEpic() {},
    createSelfEpic(state, action: PayloadAction<CreateSelfDto>) {},
    setSelf(state, action: PayloadAction<SelfApi>) {
      state.self = action.payload;
    },

    // Dream
    setDream(state, action: PayloadAction<DreamApi>) {
      state.dream = action.payload;
    },
    setDreamTitle(state, action: PayloadAction<string>) {
      state.dream.title = action.payload;
    },
    updateDreamTitleEpic(state, action: PayloadAction<UpdateDreamTitlePayload>) {},

    // Result Goal
    createResultGoalEpic(state, action: PayloadAction<CreateResultGoalPayload>) {},
    setResultGoal(state, action: PayloadAction<ResultGoalApi>) {
      state.resultGoal = action.payload;
    },
    setResultGoalTitle(state, action: PayloadAction<string>) {
      state.resultGoal.title = action.payload;
    },
    updateResultGoalTitleEpic(state, action: PayloadAction<UpdateResultGoalTitlePayload>) {},

    // Progress Goal
    createProgressGoalEpic(state, action: PayloadAction<CreateProgressGoalPayload>) {},
    setProgressGoals(state, action: PayloadAction<ProgressGoalApi[]>) {
      state.progressGoals = action.payload;
    },
    setProgressGoalTitle(state, action: PayloadAction<SetProgressGoalTitlePayload>) {
      const { idx, title } = action.payload;
      state.progressGoals[idx].title = title;
    },
    updateProgressGoalTitleEpic(state, action: PayloadAction<UpdateProgressGoalTitlePayload>) {},
    addProgressGoal(state, action: PayloadAction<ProgressGoalApi>) {
      state.progressGoals.push(action.payload);
    },
  },
});

export const {
  setSelf,
  setDream,
  setResultGoal,
  setProgressGoals,
  setDreamTitle,
  fetchSelfEpic,
  updateDreamTitleEpic,
  createSelfEpic,
  createResultGoalEpic,
  setResultGoalTitle,
  updateResultGoalTitleEpic,
  createProgressGoalEpic,
  setProgressGoalTitle,
  updateProgressGoalTitleEpic,
  addProgressGoal,
} = selfSlice.actions;
export const { reducer: selfReducer, name: selfName } = selfSlice;
