import React from 'react';
import { Input as CInput, InputProps as CInputProps } from '@chakra-ui/core';

interface InputProps extends CInputProps {}

export const Input = React.forwardRef(({ ...props }: InputProps, ref: React.Ref<any>) => {
  return <CInput {...props} focusBorderColor="primary-200" errorBorderColor="alert-300" ref={ref} />;
});
