import axios, { AxiosInstance } from 'axios';
import { API_SERVER_URL } from './../../config.json';
import { Store } from '../store/root.store';
import { ajax } from 'rxjs/ajax';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';

export class RequestService {
  private static bearerToken: string;

  static init(store: Store) {
    store.subscribe(() => {
      const { auth0 } = store.getState();
      RequestService.bearerToken = auth0.idToken;
      return () => {};
    });
  }

  static getInstance(): AxiosInstance {
    return axios.create({
      baseURL: API_SERVER_URL,
      headers: RequestService.getHeaders(),
    });
  }

  static get<T>(path: string): Observable<T> {
    return ajax.getJSON<T>(
      RequestService.getFullUrl(path),
      RequestService.getHeaders(),
    );
  }

  static delete<T>(path: string, body: any): Observable<T> {
    return ajax
      .delete(RequestService.getFullUrl(path), RequestService.getHeaders())
      .pipe(map(response => response.response));
  }

  static post<T>(path: string, body: any): Observable<T> {
    return ajax
      .post(RequestService.getFullUrl(path), body, RequestService.getHeaders())
      .pipe(map(response => response.response));
  }

  static put<T>(path: string, body: any): Observable<T> {
    return ajax
      .put(RequestService.getFullUrl(path), body, RequestService.getHeaders())
      .pipe(map(response => response.response));
  }

  private static getHeaders() {
    return { Authorization: `Bearer ${RequestService.bearerToken}` };
  }

  private static getFullUrl(path: string) {
    return API_SERVER_URL + path;
  }
}
