import React, { ReactNode } from 'react';
import { Button, ButtonProps } from '@chakra-ui/core';

export interface PrimaryOutlineButtonProps extends ButtonProps {
  children: ReactNode;
}

export const PrimaryOutlineButton = ({ children, ...props }: PrimaryOutlineButtonProps) => {
  return (
    <Button fontSize="sm" color="primary-500" variant="outline" {...props}>
      {children}
    </Button>
  );
};
