import React, { useState } from 'react';
import { FormControl, FormHelperText, FormLabel, ModalBody, ModalFooter } from '@chakra-ui/core';
import { Input } from '../../../../shared/components/Input';
import { PrimaryOutlineButton } from '../../../../shared/components/PrimaryOutlineButton';
import { Modal } from '../../../../shared/components/Modal';
import { DreamApi } from '@selfplat/api-model';
import { AdviserQuote } from '../../../../shared/components/AdviserQuote';

interface CreateResultGoalModalProps {
  dream: DreamApi;
  isOpen: boolean;
  onClose: () => void;
  onSubmitForm: (resultGoalTitle: string) => void;
}

export const CreateResultGoalModal = ({ isOpen, onClose, onSubmitForm, dream }: CreateResultGoalModalProps) => {
  const [resultGoalValue, setResultGoalValue] = useState('');

  const onResultGoalValueChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setResultGoalValue(event.target.value);
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose}>
      <ModalBody mt={8} p={{ xs: 3, lg: 8 }} display="flex" flexDirection="column" justifyContent="space-between">
        <AdviserQuote mb={5}>{dream.title}</AdviserQuote>

        <FormControl>
          <FormLabel htmlFor="resultGoal">Result Goal:</FormLabel>
          <Input id="resultGoal" value={resultGoalValue} onChange={onResultGoalValueChange} />
          <FormHelperText>ex. I will release selfplat (MVP) by the end of June</FormHelperText>
        </FormControl>
      </ModalBody>

      <ModalFooter>
        <PrimaryOutlineButton isDisabled={!resultGoalValue} onClick={() => onSubmitForm(resultGoalValue)}>
          Create
        </PrimaryOutlineButton>
      </ModalFooter>
    </Modal>
  );
};
