import React, { useState } from 'react';
import { RouteComponentProps } from '@reach/router';
import {
  Box,
  Button,
  Flex,
  FormControl,
  FormLabel,
  Heading,
} from '@chakra-ui/core';
import { ColorResult, GithubPicker } from 'react-color';
import { useDispatch, useSelector } from 'react-redux';
import { setSettings } from '../../shared/store/user/user.slice';
import {
  getDemoColors,
  getThemeColorKey,
  getThemeColors,
  ThemeColorKey,
} from '../../shared/styles/theme-colors';
import { UserSelector } from '../../shared/store/user/user.selector';
import { UserService } from '../../shared/store/user/user.service';

export interface SettingsPageProps extends RouteComponentProps {}

export const SettingsPage = (props: SettingsPageProps) => {
  const dispatch = useDispatch();
  const themeColor = useSelector(UserSelector.themeColor);
  const [selectedColor, setSelectedColor] = useState(
    getThemeColors(themeColor as ThemeColorKey)['primary-500'],
  );

  const onColorChange = (colorResult: ColorResult) => {
    setSelectedColor(colorResult.hex);
  };

  const onUpdateSettings = () => {
    const themeColor = getThemeColorKey(selectedColor);
    dispatch(setSettings({ themeColor }));
    UserService.updateSettings({ themeColor }).subscribe();
  };

  return (
    <Flex direction="column">
      <Heading as="h1" fontWeight="normal" letterSpacing={0.5} size="md" mb={5}>
        Settings
      </Heading>

      <Flex direction="column">
        <Flex direction="column" p={16} bg="white" shadow="sm">
          <Box alignSelf="center">
            <FormControl>
              <FormLabel htmlFor="firstName" mb={1}>
                Theme color:
              </FormLabel>
              <GithubPicker
                color={selectedColor}
                triangle="hide"
                onChange={onColorChange}
                colors={getDemoColors()}
              />
            </FormControl>

            <Flex mt={10} justifyContent="center">
              <Button
                onClick={onUpdateSettings}
                alignSelf="flex-start"
                fontSize="sm"
                color="primary-500"
                variant="outline"
              >
                Save
              </Button>
            </Flex>
          </Box>
        </Flex>
      </Flex>
    </Flex>
  );
};
