import { AppEpic } from '../root.epic';
import { debounceTime, distinctUntilChanged, filter, mapTo, switchMap, switchMapTo } from 'rxjs/operators';
import {
  updateDreamTitleEpic,
  fetchSelfEpic,
  setSelf,
  setResultGoal,
  setProgressGoals,
  setDream,
  createSelfEpic,
  createResultGoalEpic,
  updateResultGoalTitleEpic,
  createProgressGoalEpic,
  addProgressGoal,
  updateProgressGoalTitleEpic,
} from './self.slice';
import { concat, of } from 'rxjs';
import { SelfService } from './self.service';
import * as _ from 'lodash';
import { loadingDelay$ } from '../../observables/constants';
import { setIsLoading, setIsSaving } from '../dashboard-ui/dashboard-ui.slice';
import { PayloadAction } from '@reduxjs/toolkit';
import { UpdateDreamTitlePayload, UpdateProgressGoalTitlePayload, UpdateResultGoalTitlePayload } from './self.type';

const fetchSelf$: AppEpic = action$ =>
  action$.pipe(
    filter(fetchSelfEpic.match),
    switchMap(() =>
      concat(
        of(setIsLoading(true)),
        SelfService.getSelf().pipe(
          switchMap(({ self, dream, resultGoal, progressGoals }) =>
            loadingDelay$.pipe(
              switchMapTo(
                concat(
                  of(setSelf(self)),
                  of(setDream(dream)),
                  of(setResultGoal(resultGoal)),
                  of(setProgressGoals(progressGoals)),
                ),
              ),
            ),
          ),
        ),
        of(setIsLoading(false)),
      ),
    ),
  );

const createSelf$: AppEpic = action$ =>
  action$.pipe(
    filter(createSelfEpic.match),
    switchMap(action =>
      concat(
        of(setIsLoading(true)),
        SelfService.createSelf({ title: action.payload.title }).pipe(
          switchMap(({ self, dream }) =>
            loadingDelay$.pipe(switchMapTo(concat(of(setSelf(self)), of(setDream(dream))))),
          ),
        ),
        of(setIsLoading(false)),
      ),
    ),
  );

const updateDreamTitle$: AppEpic = action$ =>
  action$.pipe(
    filter(updateDreamTitleEpic.match),
    debounceTime(500),
    distinctUntilChanged(_.isEqual),
    switchMap((action: PayloadAction<UpdateDreamTitlePayload>) =>
      concat(
        of(setIsSaving(true)),
        SelfService.updateDream(action.payload.id, {
          title: action.payload.title,
        }).pipe(switchMapTo(loadingDelay$), mapTo(setIsSaving(false))),
      ),
    ),
  );

const createResultGoal$: AppEpic = action$ =>
  action$.pipe(
    filter(createResultGoalEpic.match),
    switchMap(action =>
      concat(
        of(setIsLoading(true)),
        SelfService.createResultGoal({
          dreamId: action.payload.dreamId,
          title: action.payload.title,
        }).pipe(switchMap(resultGoal => loadingDelay$.pipe(mapTo(setResultGoal(resultGoal))))),
        of(setIsLoading(false)),
      ),
    ),
  );

const updateResultGoalTitle$: AppEpic = action$ =>
  action$.pipe(
    filter(updateResultGoalTitleEpic.match),
    debounceTime(500),
    distinctUntilChanged(_.isEqual),
    switchMap((action: PayloadAction<UpdateResultGoalTitlePayload>) =>
      concat(
        of(setIsSaving(true)),
        SelfService.updateResultGoal(action.payload.id, {
          title: action.payload.title,
        }).pipe(switchMapTo(loadingDelay$), mapTo(setIsSaving(false))),
      ),
    ),
  );

const createProgressGoal$: AppEpic = action$ =>
  action$.pipe(
    filter(createProgressGoalEpic.match),
    switchMap(action =>
      concat(
        of(setIsLoading(true)),
        SelfService.createProgressGoal(action.payload).pipe(
          switchMap(progressGoal => loadingDelay$.pipe(mapTo(addProgressGoal(progressGoal)))),
        ),
        of(setIsLoading(false)),
      ),
    ),
  );

const updateProgressGoalTitle$: AppEpic = action$ =>
  action$.pipe(
    filter(updateProgressGoalTitleEpic.match),
    debounceTime(500),
    distinctUntilChanged(_.isEqual),
    switchMap((action: PayloadAction<UpdateProgressGoalTitlePayload>) =>
      concat(
        of(setIsSaving(true)),
        SelfService.updateProgressGoal(action.payload.id, { title: action.payload.title }).pipe(
          switchMapTo(loadingDelay$),
          mapTo(setIsSaving(false)),
        ),
      ),
    ),
  );

export const selfEpic = [
  fetchSelf$,
  updateDreamTitle$,
  createSelf$,
  createResultGoal$,
  updateResultGoalTitle$,
  createProgressGoal$,
  updateProgressGoalTitle$,
];
