import React from 'react';
import { Box, List, ListItem, Divider } from '@chakra-ui/core';
import { NavLink } from '../shared/components/NavLink';
import { useDispatch, useSelector } from 'react-redux';
import * as _ from 'lodash';
import { SelfSelector } from '../shared/store/self/self.selector';
import { DashboardUiSelector } from '../shared/store/dashboard-ui/dashboard-ui.selector';
import { Transition } from 'react-spring/renderprops';
import { useTheme } from '@chakra-ui/core';
import { toggleSideNav } from '../shared/store/dashboard-ui/dashboard-ui.slice';

interface DashboardSideNavProps {
  pathname: string;
}

export const DashboardSideNav = ({ pathname }: DashboardSideNavProps) => {
  const dispatch = useDispatch();
  const sideNavOpened = useSelector(DashboardUiSelector.sideNavOpened);

  const self = useSelector(SelfSelector.self);
  const hasSelf = !_.isEmpty(self);

  const theme = useTheme();
  const width = theme.sizes['56'];

  const onToggleSideNav = () => {
    // Solve the flickering issue
    dispatch(toggleSideNav());
  };

  return (
    <Transition
      items={sideNavOpened}
      config={{ duration: 200 }}
      from={{ transform: `translate(-${width},0)` }}
      enter={{ transform: 'translate(0,0)' }}
      leave={{ transform: `translate(-${width},0)` }}
    >
      {sideNavOpened => props => (
        <Box
          display={{ xs: sideNavOpened ? 'block' : 'none', xl: 'block' }}
          position={{ xs: 'absolute', xl: 'inherit' }}
          height={{ xs: '100%', xl: 'auto' }}
          minW={width}
          maxW={width}
          shadow="md"
          zIndex={10}
          bg="primary-50"
          p={6}
          fontSize="sm"
          transform={{ xs: props.transform, xl: 'translate(0,0)' }}
        >
          <List>
            <ListItem onClick={onToggleSideNav}>
              <NavLink to="goals">Goals</NavLink>
            </ListItem>
            {hasSelf && (
              <ListItem mt={4} onClick={onToggleSideNav}>
                <NavLink to="plans">Plans</NavLink>
              </ListItem>
            )}
            {hasSelf && (
              <ListItem mt={4} onClick={onToggleSideNav}>
                <NavLink to="daily-actions">Daily Actions</NavLink>
              </ListItem>
            )}
            {hasSelf && (
              <ListItem mt={4} onClick={onToggleSideNav}>
                <NavLink to="weekly-summary">Weekly Summary</NavLink>
              </ListItem>
            )}

            <Divider my={5} />

            <ListItem mt={4} onClick={onToggleSideNav}>
              <NavLink to="gratitude">Gratitude</NavLink>
            </ListItem>

            <Divider my={5} />

            <ListItem onClick={onToggleSideNav}>
              <NavLink to="profile">My Profile</NavLink>
            </ListItem>
            <ListItem mt={4} onClick={onToggleSideNav}>
              <NavLink to="settings">Settings</NavLink>
            </ListItem>
          </List>
        </Box>
      )}
    </Transition>
  );
};
