import React from 'react';
import { useDisclosure } from '@chakra-ui/core';
import { useDispatch } from 'react-redux';
import { CreateDreamModal } from './CreateDreamModal/CreateDreamModal';
import { PageTitle } from '../../../shared/components/PageTitle';
import { FlexCard } from '../../../shared/components/FlexCard';
import { PageContainer } from '../../../shared/components/PageContainer';
import { PrimaryOutlineButton } from '../../../shared/components/PrimaryOutlineButton';
import { Quote } from '../../../shared/components/Quote';
import { AdviserCard } from '../../../shared/components/AdviserCard';
import { AdviserQuote } from '../../../shared/components/AdviserQuote';
import { SimpleGrid } from '@chakra-ui/core/dist';
import { createSelfEpic } from '../../../shared/store/self/self.slice';

export const WelcomeSubPage = () => {
  const dispatch = useDispatch();

  const { isOpen: isModalOpen, onOpen: onOpenModal, onClose: onCloseModal } = useDisclosure();

  const onSubmitForm = (dreamTitle: string) => {
    dispatch(createSelfEpic({ title: dreamTitle }));
  };

  return (
    <PageContainer>
      <PageTitle>Get Started</PageTitle>

      <SimpleGrid columns={{ xs: 1, lg: 2 }} spacing={10}>
        <AdviserCard>
          <AdviserQuote>Welcome to SelfPlat, this platform is carefully crafted for you.</AdviserQuote>
          <AdviserQuote>It's very simple but powerful.</AdviserQuote>
          <AdviserQuote>Everything is about consistent.</AdviserQuote>
          <AdviserQuote>your dream will be achieved if you keep working on it everyday.</AdviserQuote>
          <AdviserQuote>Are you ready to start this long journey?</AdviserQuote>
          <PrimaryOutlineButton mt={3} onClick={onOpenModal} alignSelf="flex-end">
            Get started
          </PrimaryOutlineButton>
        </AdviserCard>

        <FlexCard justifyContent="center" alignItems="center">
          <Quote
            quote={
              <>
                A <b>dream</b> written down with a date becomes a goal. <br />A <b>goal</b> broken down into{' '}
                <b>steps</b> becomes a plan. <br />A <b>plan</b> backed by <b>action</b> makes your dreams come true.
              </>
            }
            author="Greg Reid"
          />
        </FlexCard>
      </SimpleGrid>

      <CreateDreamModal isOpen={isModalOpen} onClose={onCloseModal} onSubmitForm={onSubmitForm} />
    </PageContainer>
  );
};
