export const getDemoColors = (): string[] => {
  return _.keys(themeColors).map((colorKey: string) => {
    return themeColors[colorKey as ThemeColorKey]['primary-500'];
  });
};

export const getThemeColorKey = (color: string): ThemeColorKey => {
  const colorKey = _.keys(themeColors).find((colorKey: string) => {
    return themeColors[colorKey as ThemeColorKey]['primary-500'].toLowerCase() === color.toLowerCase();
  });
  return colorKey as ThemeColorKey;
};

export const getThemeColors = (colorKey: ThemeColorKey): ThemeColors => {
  return themeColors[colorKey] as ThemeColors;
};

export type ThemeColorKey = keyof typeof themeColors;
export type ThemeColors = typeof themeColors.deepOrange;
