import {
  CreateResultGoalDto,
  CreateSelfApi,
  CreateSelfDto,
  DreamApi,
  GetSelfApi,
  ProgressGoalApi,
  ResultGoalApi,
  UpdateDreamDto,
  UpdateProgressGoalDto,
  UpdateResultGoalDto,
} from '@selfplat/api-model';
import { RequestService } from '../../services/request.service';
import { Observable } from 'rxjs';

export class SelfService {
  static getSelf(): Observable<GetSelfApi> {
    return RequestService.get<GetSelfApi>('self');
  }

  static createSelf(dto: CreateSelfDto): Observable<CreateSelfApi> {
    return RequestService.post<CreateSelfApi>('self', dto);
  }

  static updateDream(id: string, dto: UpdateDreamDto): Observable<DreamApi> {
    return RequestService.post<DreamApi>(`dream/${id}`, dto);
  }

  static createResultGoal(dto: CreateResultGoalDto): Observable<ResultGoalApi> {
    return RequestService.post<ResultGoalApi>('result-goal', dto);
  }

  static updateResultGoal(id: string, dto: UpdateResultGoalDto): Observable<ResultGoalApi> {
    return RequestService.put<DreamApi>(`result-goal/${id}`, dto);
  }

  static createProgressGoal(dto: CreateResultGoalDto): Observable<ProgressGoalApi> {
    return RequestService.post<ProgressGoalApi>('progress-goal', dto);
  }

  static updateProgressGoal(id: string, dto: UpdateProgressGoalDto): Observable<ProgressGoalApi> {
    return RequestService.put<ProgressGoalApi>(`progress-goal/${id}`, dto);
  }
}
