import React, { useState } from 'react';

import { Button, Flex, Modal, ModalContent, ModalOverlay } from '@chakra-ui/core';
import { ModalBody } from '@chakra-ui/core/dist';
import AvatarEdit from 'react-avatar-edit';

export interface ImageCropModalProps {
  isOpen: boolean;
  onClose: () => void;
  pictureUrl: string;
  onSaveImage: (imageBlob: string) => void;
}

export const ImageCropModal = ({ isOpen, onClose, pictureUrl, onSaveImage }: ImageCropModalProps) => {
  const [imageBlob, setImageBlob] = useState();

  const onCloseImage = () => {
    setImageBlob(null);
  };

  const onCropImage = (imageBlob: string) => {
    setImageBlob(imageBlob);
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose} closeOnOverlayClick={false}>
      <ModalOverlay />
      <ModalContent width="400px">
        <ModalBody m={0} p={0}>
          <AvatarEdit
            onCrop={onCropImage}
            onClose={onCloseImage}
            src={pictureUrl}
            height={400}
            width={400}
            imageWidth={400}
          />
          <Flex p={3}>
            <Button onClick={onClose} width="100%" fontSize="sm" color="alert-500" variant="outline" mr={3}>
              Cancel
            </Button>
            <Button
              onClick={() => {
                onClose();
                onSaveImage(imageBlob);
              }}
              width="100%"
              fontSize="sm"
              color="primary-500"
              variant="outline"
              isDisabled={!imageBlob}
            >
              Save
            </Button>
          </Flex>
        </ModalBody>
      </ModalContent>
    </Modal>
  );
};
