import React, { ReactNode, useEffect } from 'react';
import { Flex, Spinner } from '@chakra-ui/core';
import { DashboardSideNav } from './DashboardSideNav';
import { RouteComponentProps } from '@reach/router';
import { DashboardHeader } from './DashboardHeader';
import { useDispatch, useSelector } from 'react-redux';
import { fetchSelfEpic } from '../shared/store/self/self.slice';
import { DashboardUiSelector } from '../shared/store/dashboard-ui/dashboard-ui.selector';

interface DashboardProps extends RouteComponentProps {
  children: ReactNode;
}

export const Dashboard = ({ children, location }: DashboardProps) => {
  const { pathname } = location;
  const dispatch = useDispatch();

  const isLoading = useSelector(DashboardUiSelector.isLoading);
  const isSaving = useSelector(DashboardUiSelector.isSaving);

  useEffect(() => {
    dispatch(fetchSelfEpic());
  }, []);

  return (
    <>
      <DashboardHeader />
      <Flex flexGrow={1} position={{ xs: 'relative', xl: 'inherit' }}>
        <DashboardSideNav pathname={pathname} />
        <Flex flexDirection="column" flexGrow={1} p={[5, 5, 12]} bg="grey-50">
          {!isLoading && children}
          {(isLoading || isSaving) && (
            <Spinner
              position="absolute"
              right={[5, 5, 12]}
              bottom={[5, 5, 12]}
              color="primary-500"
              size="lg"
            />
          )}
        </Flex>
      </Flex>
    </>
  );
};
