import React, { ReactNode } from 'react';
import { Divider, Text, BoxProps } from '@chakra-ui/core';
import { FlexCard } from './FlexCard';

export interface EntityCardProps extends BoxProps {
  children: ReactNode;
  entityTite: string;
}

export const EntityCard = ({
  children,
  entityTite,
  ...props
}: EntityCardProps) => {
  return (
    <FlexCard {...props}>
      {children}
      <Divider mt={0} mb={2} borderColor="primary-500" borderWidth={2} />
      <Text
        alignSelf="flex-end"
        fontSize="xs"
        fontWeight="bold"
        letterSpacing={1}
      >
        {entityTite}
      </Text>
    </FlexCard>
  );
};
