import * as _ from 'lodash';
import { RootState } from '../store/root.reducer';
import { auth0Name } from '../store/auth0/auth0.slice';
import { userName } from '../store/user/user.slice';
import { dashboardUiName } from '../store/dashboard-ui/dashboard-ui.slice';
import { LocalStorageUtil } from '../utils/local-storage.util';
import { Store } from '../store/root.store';

export class StoreService {
  static init(store: Store, storeKey: string) {
    store.subscribe(() => {
      StoreService.saveStateToLocalStorage(store.getState(), storeKey);
      return () => {};
    });
  }

  private static saveStateToLocalStorage = _.debounce(
    (state: RootState, storeKey: string) => {
      const slicesNamesToSave = [auth0Name, userName, dashboardUiName];
      const stateToSave = slicesNamesToSave.reduce((stateToSave, sliceName) => {
        stateToSave[sliceName] = state[sliceName];
        return stateToSave;
      }, {} as RootState);
      LocalStorageUtil.set(storeKey, stateToSave);
    },
    300,
  );
}
