export class LocalStorageUtil {
  private static readonly prefix = 'selfplat-';

  static set(key: string, value: any) {
    try {
      localStorage.setItem(LocalStorageUtil.getKeyWithPrefix(key), JSON.stringify(value));
    } catch (e) {
      console.error('LocalStorageUtil.set', e);
    }
  }

  static get<T = any>(key: string): T {
    try {
      const json = localStorage.getItem(LocalStorageUtil.getKeyWithPrefix(key));
      return JSON.parse(json);
    } catch (e) {
      console.error('LocalStorageUtil.get', e);
    }
  }

  private static getKeyWithPrefix(key: string) {
    return `${LocalStorageUtil.prefix}-${key}`;
  }
}
