import React from 'react';
import { PageTitle } from '../../../shared/components/PageTitle';
import { PageContainer } from '../../../shared/components/PageContainer';
import { SimpleGrid, Textarea, IconButton, useDisclosure, Flex, Icon, Divider } from '@chakra-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { SelfSelector } from '../../../shared/store/self/self.selector';
import {
  createResultGoalEpic,
  setDreamTitle,
  updateDreamTitleEpic,
  setResultGoalTitle,
  updateResultGoalTitleEpic,
  createProgressGoalEpic,
  setProgressGoalTitle,
  updateProgressGoalTitleEpic,
} from '../../../shared/store/self/self.slice';
import { EntityCard } from '../../../shared/components/EntityCard';
import { CreateResultGoalModal } from './CreateResultGoalModal/CreateResultGoalModal';
import { CreateProgressGoalModal } from './CreateProgressGoalModal/CreateProgressGoalModal';

export const GoalsSubPage = () => {
  const dispatch = useDispatch();
  const dream = useSelector(SelfSelector.dream);
  const resultGoal = useSelector(SelfSelector.resultGoal);
  const progressGoals = useSelector(SelfSelector.progressGoals);

  const {
    isOpen: isCreateResultGoalModalOpen,
    onOpen: onOpenCreateResultGoalModal,
    onClose: onCloseCreateResultGoalModal,
  } = useDisclosure();

  const {
    isOpen: isCreateProgressGoalModalOpen,
    onOpen: onOpenProgressResultGoalModal,
    onClose: onCloseProgressResultGoalModal,
  } = useDisclosure();

  const onUpdateDreamTitle = (event: React.ChangeEvent<HTMLInputElement>) => {
    dispatch(setDreamTitle(event.target.value));
    dispatch(updateDreamTitleEpic({ id: dream.id, title: event.target.value }));
  };

  const onUpdateResultGoalTitle = (event: React.ChangeEvent<HTMLInputElement>) => {
    dispatch(setResultGoalTitle(event.target.value));
    dispatch(updateResultGoalTitleEpic({ id: resultGoal.id, title: event.target.value }));
  };

  const onCreateResultGoal = (resultGoalTitle: string) => {
    onCloseCreateResultGoalModal();
    dispatch(createResultGoalEpic({ dreamId: dream.id, title: resultGoalTitle }));
  };

  const onUpdateProgressGoalTitle = (id: string, idx: number) => (event: React.ChangeEvent<HTMLInputElement>) => {
    dispatch(setProgressGoalTitle({ idx, title: event.target.value }));
    dispatch(updateProgressGoalTitleEpic({ id: id, title: event.target.value }));
  };

  const onCreateProgressGoal = (progressGoalTitle: string) => {
    onCloseProgressResultGoalModal();
    dispatch(createProgressGoalEpic({ dreamId: dream.id, resultGoalId: resultGoal.id, title: progressGoalTitle }));
  };

  return (
    <PageContainer>
      <PageTitle subtitle="A dream written down with a date becomes a goal.">Goals</PageTitle>

      <SimpleGrid columns={1}>
        <EntityCard entityTite="Dream">
          <Textarea
            variant="unstyled"
            resize="none"
            rows={2}
            minH="auto"
            value={dream.title}
            onChange={onUpdateDreamTitle}
          />
        </EntityCard>

        <Flex my={1} justifyContent="center" alignItems="center">
          <Icon name="arrow-down" size="5" color="primary-500" />
        </Flex>

        <EntityCard entityTite="Result Goal">
          {!resultGoal && (
            <IconButton
              alignSelf="flex-end"
              aria-label="create result goal"
              variant="outline"
              color="primary-500"
              icon="add"
              mb={2}
              onClick={onOpenCreateResultGoalModal}
            />
          )}
          {resultGoal && (
            <Textarea
              variant="unstyled"
              resize="none"
              rows={2}
              minH="auto"
              value={resultGoal.title}
              onChange={onUpdateResultGoalTitle}
            />
          )}
        </EntityCard>

        <Flex my={1} justifyContent="center" alignItems="center">
          <Icon name="arrow-down" size="5" color="primary-500" />
        </Flex>

        {resultGoal && (
          <EntityCard entityTite="Progress Goals">
            <IconButton
              alignSelf="flex-end"
              aria-label="create progress goal"
              variant="outline"
              color="primary-500"
              icon="add"
              onClick={onOpenProgressResultGoalModal}
            />

            {progressGoals.map(({ id, title }, idx) => (
              <>
                <Textarea
                  id={id}
                  variant="unstyled"
                  resize="none"
                  rows={2}
                  minH="auto"
                  value={title}
                  borderBottomWidth={idx !== progressGoals.length - 1 ? 1 : 0}
                  borderColor="grey-100"
                  borderRadius={0}
                  onChange={onUpdateProgressGoalTitle(id, idx)}
                />
              </>
            ))}
          </EntityCard>
        )}
      </SimpleGrid>

      <CreateResultGoalModal
        isOpen={isCreateResultGoalModalOpen}
        onClose={onCloseCreateResultGoalModal}
        onSubmitForm={onCreateResultGoal}
        dream={dream}
      />

      {resultGoal && (
        <CreateProgressGoalModal
          dream={dream}
          resultGoal={resultGoal}
          isOpen={isCreateProgressGoalModalOpen}
          onClose={onCloseProgressResultGoalModal}
          onSubmitForm={onCreateProgressGoal}
        />
      )}
    </PageContainer>
  );
};
