import React, { ReactNode, useEffect, useState } from 'react';
import { AUTH0_CLIENT_ID, AUTH0_DOMAIN } from '../../config.json';
import Auth0Lock from 'auth0-lock';
import { createContext, useContext } from 'react';
import { useDispatch } from 'react-redux';
import { setIdToken } from '../store/auth0/auth0.slice';
import { fetchUserInfoEpic } from '../store/user/user.slice';
import { useTheme } from '@chakra-ui/core';

interface Auth0ContextState {
  lock: Auth0LockStatic;
}

interface AUth0ProviderProps {
  children: ReactNode;
}

const Auth0Context = createContext<Auth0ContextState>(null);

export const useAuth0 = () => useContext(Auth0Context);

export const Auth0Provider = ({ children }: AUth0ProviderProps) => {
  const [lock, setLock] = useState<Auth0LockStatic>(null);
  const dispatch = useDispatch();

  const theme = useTheme();

  useEffect(() => {
    const auth0Lock = new Auth0Lock(AUTH0_CLIENT_ID, AUTH0_DOMAIN, {
      theme: {
        hideMainScreenTitle: true,
        logo: '',
        primaryColor: theme.colors['primary-500'],
      },
      auth: {
        responseType: 'token id_token',
        params: {
          scope: 'openid profile email',
        },
      },
    });

    setLock(auth0Lock);

    auth0Lock.on('authenticated', authResult => {
      const { idToken } = authResult;
      dispatch(setIdToken(idToken));
      dispatch(fetchUserInfoEpic());
    });
  }, []);

  return (
    <Auth0Context.Provider value={{ lock }}>{children}</Auth0Context.Provider>
  );
};
