import React, { ReactNode } from 'react';
import {
  ModalCloseButton,
  ModalContent,
  ModalOverlay,
  Modal as CModal,
} from '@chakra-ui/core';
import { Transition } from 'react-spring/renderprops';

interface ModalProps {
  isOpen: boolean;
  onClose: () => void;
  children: ReactNode;
}

export const Modal = ({ isOpen, onClose, children }: ModalProps) => {
  return (
    <Transition
      items={isOpen}
      config={{ duration: 200 }}
      from={{ opacity: 0, transform: `translate3d(0, 10px, 0)` }}
      enter={{ opacity: 1, transform: `translate3d(0, 0, 0)` }}
      leave={{ opacity: 0, transform: `translate3d(0, 10px, 0)` }}
    >
      {isOpen => props => (
        <CModal isOpen={isOpen} onClose={onClose} closeOnOverlayClick={false}>
          <ModalOverlay {...props} />
          <ModalContent
            {...props}
            minW={{ xs: '90%', sm: '75%', lg: 'xl' }}
            maxW={{ xs: '90%', sm: '75%', lg: 'xl' }}
          >
            <ModalCloseButton right={{ xs: 2, lg: 5 }} />
            {children}
          </ModalContent>
        </CModal>
      )}
    </Transition>
  );
};
