import React from 'react';
import { RouteComponentProps } from '@reach/router';
import { useSelector } from 'react-redux';
import * as _ from 'lodash';
import { WelcomeSubPage } from './WelcomeSubPage/WelcomeSubPage';
import { SelfSelector } from '../../shared/store/self/self.selector';
import { GoalsSubPage } from './GoalsSubPage/GoalsSubPage';

export interface GoalsPageProps extends RouteComponentProps {}

export const GoalsPage = (props: GoalsPageProps) => {
  const self = useSelector(SelfSelector.self);

  if (_.isEmpty(self)) {
    return <WelcomeSubPage />;
  }

  return <GoalsSubPage />;
};
