import { AppEpic } from '../root.epic';
import { filter, map, switchMap, tap } from 'rxjs/operators';
import { UserService } from './user.service';
import { navigate } from '@reach/router';
import { setProfile, setSettings, fetchUserInfoEpic } from './user.slice';
import { concat } from 'rxjs';

const fetchUsers$: AppEpic = action$ =>
  action$.pipe(
    filter(fetchUserInfoEpic.match),
    switchMap(() =>
      concat(
        UserService.getProfile().pipe(map(profile => setProfile(profile))),
        UserService.getSettings().pipe(map(settings => setSettings(settings))),
      ),
    ),
    // Is it a good way to do this
    tap(() => navigate('/dashboard')),
  );

export const userEpic = [fetchUsers$];
