import { Link as RLink, LinkGetProps } from '@reach/router';
import React, { ReactNode, useState } from 'react';
import { useTheme } from '@chakra-ui/core';

interface NavLinkProps {
  to: string;
  children: ReactNode;
  color?: string;
  hoverColor?: string;
  small?: boolean;
}

export const NavLink = ({ to, children, color, hoverColor, small }: NavLinkProps) => {
  const theme = useTheme();

  const [isHover, setHover] = useState(false);

  const usedHoverColor = hoverColor || theme.colors['primary-500'];

  const defaultStyles = {
    color: isHover ? usedHoverColor : color,
    transition: 'all 0.15s ease-out',
    fontSize: small ? theme.fontSizes.sm : theme.fontSizes.md,
  };

  const isPartiallyActive = ({ isPartiallyCurrent }: LinkGetProps) => {
    return isPartiallyCurrent ? { style: { ...defaultStyles, color: usedHoverColor } } : {};
  };

  return (
    <RLink
      to={to}
      getProps={isPartiallyActive}
      onMouseEnter={() => setHover(true)}
      onMouseLeave={() => setHover(false)}
      style={{
        ...defaultStyles,
      }}
    >
      {children}
    </RLink>
  );
};
