import React, { ReactNode } from 'react';
import { Box, PseudoBox, Divider, Flex } from '@chakra-ui/core';

interface QuoteProps {
  quote: string | ReactNode;
  author?: string | ReactNode;
}

export const Quote = ({ quote, author }: QuoteProps) => {
  return (
    <Flex lineHeight={3}>
      <Divider orientation="vertical" borderWidth={[5, 5, 8, 8]} borderColor="primary-500" />
      <Box>
        <PseudoBox
          as="blockquote"
          fontStyle="italic"
          px={{ xs: 3, md: 24, lg: 10 }}
          pt={{ xs: 3, lg: 5 }}
          mb={3}
          letterSpacing={{ xs: 0.2, lg: 0.5 }}
        >
          {quote}
        </PseudoBox>
        {author && (
          <PseudoBox
            as="cite"
            display="block"
            textAlign="right"
            pr={3}
            letterSpacing={{ xs: 0.5, lg: 1 }}
            _before={{ content: "' '" } as any}
          >
            {author}
          </PseudoBox>
        )}
      </Box>
    </Flex>
  );
};
