import { FormControl, FormHelperText, FormLabel, ModalBody, ModalFooter } from '@chakra-ui/core';
import React, { useState } from 'react';
import { PrimaryOutlineButton } from '../../../../shared/components/PrimaryOutlineButton';
import { Input } from '../../../../shared/components/Input';
import { Modal } from '../../../../shared/components/Modal';

interface CreateDreamModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmitForm: (dreamTitle: string) => void;
}

export const CreateDreamModal = ({ isOpen, onClose, onSubmitForm }: CreateDreamModalProps) => {
  const [dreamValue, setDreamValue] = useState('');

  const onDreamValueChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setDreamValue(event.target.value);
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose}>
      <ModalBody
        mt={8}
        p={{ xs: 3, lg: 8 }}
        minH={64}
        display="flex"
        flexDirection="column"
        justifyContent="space-between"
      >
        <FormControl>
          <FormLabel htmlFor="dream">Dream:</FormLabel>
          <Input id="dream" value={dreamValue} onChange={onDreamValueChange} />
          <FormHelperText>ex. I would like to have freedom in money and time</FormHelperText>
        </FormControl>

        <FormControl isDisabled>
          <Input textAlign="center" fontSize="small" value={new Date().toDateString()} />
        </FormControl>
      </ModalBody>

      <ModalFooter>
        <PrimaryOutlineButton isDisabled={!dreamValue} onClick={() => onSubmitForm(dreamValue)}>
          Get Started
        </PrimaryOutlineButton>
      </ModalFooter>
    </Modal>
  );
};
