import React, { ReactNode } from 'react';
import { PseudoBoxProps, PseudoBox } from '@chakra-ui/core';

interface AdviserQuoteProps extends PseudoBoxProps {
  children: ReactNode;
}

export const AdviserQuote = ({ children, ...props }: AdviserQuoteProps) => {
  return (
    <PseudoBox
      as="blockquote"
      letterSpacing={{ xs: 0.5, lg: 1 }}
      lineHeight={{ xs: 1.6, lg: 2 }}
      p={2}
      _before={{ content: 'open-quote', color: 'primary-800', fontWeight: 'bold', mr: 1 } as any}
      _after={{ content: 'close-quote', color: 'primary-800', fontWeight: 'bold', ml: 1 } as any}
      {...props}
    >
      {children}
    </PseudoBox>
  );
};
