import React, { useState } from 'react';
import { Flex, FormControl, FormHelperText, FormLabel, Icon, ModalBody, ModalFooter } from '@chakra-ui/core';
import { Input } from '../../../../shared/components/Input';
import { PrimaryOutlineButton } from '../../../../shared/components/PrimaryOutlineButton';
import { Modal } from '../../../../shared/components/Modal';
import { DreamApi, ResultGoalApi } from '@selfplat/api-model';
import { AdviserQuote } from '../../../../shared/components/AdviserQuote';

interface CreateProgressGoalModalProps {
  dream: DreamApi;
  resultGoal: ResultGoalApi;
  isOpen: boolean;
  onClose: () => void;
  onSubmitForm: (progressGoalTitle: string) => void;
}

export const CreateProgressGoalModal = ({
  isOpen,
  onClose,
  onSubmitForm,
  dream,
  resultGoal,
}: CreateProgressGoalModalProps) => {
  const [progressGoalValue, setProgressGoalValue] = useState('');

  const onProgressGoalValueChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setProgressGoalValue(event.target.value);
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose}>
      <ModalBody mt={8} p={{ xs: 3, lg: 8 }} display="flex" flexDirection="column" justifyContent="space-between">
        <AdviserQuote>{dream.title}</AdviserQuote>
        <Flex my={1} justifyContent="center" alignItems="center">
          <Icon name="arrow-down" size="5" color="primary-500" />
        </Flex>
        <AdviserQuote mb={5}>{resultGoal.title}</AdviserQuote>

        <FormControl>
          <FormLabel htmlFor="progressGoal">Progress Goal:</FormLabel>
          <Input id="progressGoal" value={progressGoalValue} onChange={onProgressGoalValueChange} />
          <FormHelperText>ex. I will release selfplat (MVP) by the end of June</FormHelperText>
        </FormControl>
      </ModalBody>

      <ModalFooter>
        <PrimaryOutlineButton isDisabled={!progressGoalValue} onClick={() => onSubmitForm(progressGoalValue)}>
          Create
        </PrimaryOutlineButton>
      </ModalFooter>
    </Modal>
  );
};
