import React, { ReactNode } from 'react';
import { Flex, BoxProps } from '@chakra-ui/core';

interface FlexCardProps extends BoxProps {
  children: ReactNode;
}

export const FlexCard = ({ children, ...props }: FlexCardProps) => {
  return (
    <Flex direction="column" bg="white" shadow="sm" p={5} {...props}>
      {children}
    </Flex>
  );
};
