import { createSlice, PayloadAction } from '@reduxjs/toolkit';

export interface Auth0State {
  // use to access api-server
  idToken: string;
}

const initialState: Auth0State = {
  idToken: '',
};

const auth0Slice = createSlice({
  name: 'auth0',
  initialState,
  reducers: {
    setIdToken(state, action: PayloadAction<string>) {
      const { payload: idToken } = action;
      state.idToken = idToken;
    },
  },
});

export const { setIdToken } = auth0Slice.actions;
export const { reducer: auth0Reducer, name: auth0Name } = auth0Slice;
