import React from 'react';
import { useSelector } from 'react-redux';
import { ThemeProvider, CSSReset } from '@chakra-ui/core';
import { theme } from './shared/styles/theme';
import { Auth0Provider } from './shared/auth0/Auth0Provider';
import { Dashboard } from './Dashboard/Dashboard';
import { Redirect, Router } from '@reach/router';
import { GoalsPage } from './Dashboard/GoalsPage/GoalsPage';
import { PlansPage } from './Dashboard/PlansPage/PlansPage';
import { WeeklySummaryPage } from './Dashboard/WeeklySummaryPage/WeeklySummaryPage';
import { GratitudePage } from './Dashboard/GratitudePage/GratitudePage';
import { Home } from './Home/Home';
import { ProfilePage } from './Dashboard/ProfilePage/ProfilePage';
import { SettingsPage } from './Dashboard/SettingsPage/SettingsPage';
import { UserSelector } from './shared/store/user/user.selector';
import { getThemeColors, ThemeColorKey } from './shared/styles/theme-colors';
import { DailyActionsPage } from './Dashboard/DailyActionsPage/DailyActionsPage';

export const App = () => {
  const { themeColor } = useSelector(UserSelector.settings);

  const appTheme = {
    ...theme,
    colors: {
      ...theme.colors,
      ...getThemeColors(themeColor as ThemeColorKey),
    },
  };

  return (
    <ThemeProvider theme={appTheme}>
      <Auth0Provider>
        <CSSReset />
        <Router primary={false}>
          <Home path="/" />
          <Dashboard path="/dashboard">
            <GoalsPage path="goals" />
            <PlansPage path="plans" />
            <DailyActionsPage path="daily-actions" />
            <WeeklySummaryPage path="weekly-summary" />
            <GratitudePage path="gratitude" />
            <ProfilePage path="profile" />
            <SettingsPage path="settings" />
            <Redirect from="/" to="/dashboard/goals" default noThrow />
          </Dashboard>
          <Redirect from="/" to="/dashboard" default noThrow />
        </Router>
      </Auth0Provider>{' '}
    </ThemeProvider>
  );
};
