import { RootState } from '../root.reducer';

export class SelfSelector {
  static dreamTitle = (state: RootState) => state.self.dream?.title;
  static self = (state: RootState) => state.self.self;
  static dream = (state: RootState) => state.self.dream;
  static dreamId = (state: RootState) => state.self.dream.id;
  static resultGoal = (state: RootState) => state.self.resultGoal;
  static progressGoals = (state: RootState) => state.self.progressGoals;
}
