import {
  UserProfileApi,
  UpdateUserProfileDto,
  UserSettingsApi,
  UpdateUserSettingsDto,
} from '@selfplat/api-model';
import { RequestService } from '../../services/request.service';
import { Observable } from 'rxjs';

export class UserService {
  static getProfile(): Observable<UserProfileApi> {
    return RequestService.get<UserProfileApi>('user-profile');
  }

  static getSettings(): Observable<UserSettingsApi> {
    return RequestService.get<UserSettingsApi>('user-settings');
  }

  static updateProfile(dto: UpdateUserProfileDto): Observable<UserProfileApi> {
    return RequestService.put<UserProfileApi>('user-profile', dto);
  }

  static updateProfilePicture(formData: FormData): Observable<UserProfileApi> {
    return RequestService.post<UserProfileApi>('user-profile/image', formData);
  }

  static updateSettings(
    dto: UpdateUserSettingsDto,
  ): Observable<UserSettingsApi> {
    return RequestService.put<UserSettingsApi>('user-settings', dto);
  }
}
