import React from 'react';
import { Flex, Box, Link, MenuList, MenuDivider, Menu, MenuButton, MenuItem, Avatar } from '@chakra-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { navigate } from '@reach/router';
import { UserSelector } from '../shared/store/user/user.selector';
import { MdMenu, MdClose } from 'react-icons/md';
import { toggleSideNav } from '../shared/store/dashboard-ui/dashboard-ui.slice';
import { DashboardUiSelector } from '../shared/store/dashboard-ui/dashboard-ui.selector';

interface DashboardHeaderProps {}

export const DashboardHeader = (props: DashboardHeaderProps) => {
  const dispatch = useDispatch();
  const sideNavOpened = useSelector(DashboardUiSelector.sideNavOpened);

  const { pictureUrl, fullName } = useSelector(UserSelector.profile) || {};

  const navigateTo = (page: string) => async () => {
    await navigate(`/dashboard/${page}`);
  };

  const onToggleSideNav = () => {
    dispatch(toggleSideNav());
  };

  return (
    <Flex
      alignItems="center"
      justifyContent="space-between"
      zIndex={100}
      bg="primary-500"
      px={{ xs: 3, lg: 8 }}
      h={12}
      shadow="md"
    >
      <Flex alignItems="center">
        <Box
          display={{ xs: 'block', xl: 'none' }}
          color="white"
          fontSize="2xl"
          as={sideNavOpened ? MdClose : MdMenu}
          mr={2}
          cursor="pointer"
          onClick={onToggleSideNav}
        />
        <Link href="#" fontSize="xl" color="white" _hover={{ color: 'primary-100' }} letterSpacing={0.5}>
          SelfPlat
        </Link>
      </Flex>
      <Menu>
        <MenuButton as={Box}>
          <Avatar name={fullName} width={10} height={10} src={pictureUrl} />
        </MenuButton>
        <MenuList>
          <MenuItem onClick={navigateTo('profile')}>My Profile</MenuItem>
          <MenuItem onClick={navigateTo('settings')}>Settings</MenuItem>
          <MenuDivider />
          <MenuItem>Logout</MenuItem>
        </MenuList>
      </Menu>
    </Flex>
  );
};
